.main-button {
  background-color: #55198b;
  border: solid 1px #55198b;
  color: white;
  font-weight: 500;
  line-height: 1.1;
  padding: 13px 22px;
  margin-right: 5px;
  border-radius: 10px;
  text-align: center;
  text-decoration: none;
  font: 200 20px/1.5 Helvetica, Verdana, sans-serif;
  float: left;
  margin-top: 20px;
  font-size: 18px;
  cursor: pointer;
  letter-spacing: 2px;
}

.main-button:hover {
  transition: ease-in 0.3s;
}

.project-button {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

@media (max-width: 768px) {
  .main-button {
    font-size: 15px;
    padding: 12px 18px;
    margin-right: 0px;
  }
}

@media (max-width: 320px) {
  .main-button {
    font-size: 12px;
  }
}